import React from 'react';
import Image from '../Image/Image';
import Title from '../Title/Title';
import NavigationMenu from "../Navigation/NavigationMenu";
import Text from "../Text/Text";
import ScrollDown from "../ScrollDown/ScrollDown";

const Main = (props) => {
  return (
    <main className='main'>
      <section className='top'>
        <ScrollDown text='scroll down to see more' scrollClass='scroll-top' styleClassText='scroll-text'/>
      </section>
      <section className='main-nav'>
        <NavigationMenu addClassList='main-list' addClassItem='main-item' addClassLink='main-link'/>
      </section>
      <section className='about-me'>
        <Image alt='male 30 years old'/>
        <div className='description'>
          <Title styleClass='description-title' title='About me'/>
          <Text/>
        </div>
      </section>
      <ScrollDown text='keep scrolling, there is still more to come.' scrollClass='scroll-bottom' styleClassText='scroll-text'/>
    </main>
  )
};

export default Main;